@import "styles/colorPalette.scss";

.root {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .btn {
        display: block;
        margin: 0.5rem 0;
        margin-left: auto;
        background-color: $color-10;
        text-transform: none;
    }
    .container {
        margin: 2rem;
        background-color: white;
        width: 50vw;
        max-width: 70vw;
        height: 50vh;
        max-height: 80vh;
        overflow: scroll;
        outline: none;
        border: none;
        padding: 1rem;
        border-radius: 0.3rem;
    }
}
